import { BaseService } from "../baseService";
import StorageClient from "../../clients/storageClient";
import RouterClient from "../../clients/routerClientInstance";
import Logger from "../../clients/logger";
import ConfigClient from "../../clients/configClient";

import {
	parallel as asyncParallel,
	each as asyncEach,
	series as asyncSeries,
} from "async";
import FSBLDependencyManager from "../../common/dependencyManager";

const STORAGE_TOPIC = "finsemble";
const USER_PREFERENCES_STORAGE_KEY = "userPreferences";

//'start()?
StorageClient.initialize();
ConfigClient.initialize();
class PreferenceService extends BaseService {
	/**
	 * @param {string} params.name name of the service
	 * @param {object} params.startupDependencies
	 * @param {array} params.startupDependencies.clients Clients to wait on before initializing the service
	 * @param {array} params.startupDependencies.services Services to wait on before initializing the service
	 * @param {object} params.shutdownDependencies
	 * @param {array} params.shutdownDependencies.services Services to wait on before shutting down the service
	 **/
	constructor(params) {
		if (!params || params === undefined) params = {};
		if (!params.hasOwnProperty("startupDependencies")) {
			params.startupDependencies = {
				services: ["storageService"],
				clients: ["storageClient", "configClient"]
			};
		}

		if (!params.hasOwnProperty("shutdownDependencies")) {
			params.shutdownDependencies = {
				services: ["startupLauncherService"]
			};
		}

		super(params);
		//Usually not included. We do it here because we have two services sharing the same window. Default service name is window.name.
		this.name = "preferencesService",
		//Object of preferences.
		this.userPreferences = {};
		this.bindCorrectContext();
		this.onBaseServiceReady(this.doInit);
		this.start();
	}

	/**
	 * Creates router endpoints for all of our client APIs.
	 * @private
	 */
	createRouterEndpoints() {
		var self = this;
		RouterClient.addResponder("PreferencesService.getPreferences", (err, message) => {
			message.sendQueryResponse(err, self.userPreferences);
		});

		RouterClient.addResponder("PreferencesService.setPreference", (err, message) => {
			let preference = message.data;
			this.setPreference(preference, (err2) => {
				message.sendQueryResponse(err2, err2 ? null : "success");
			});
		});
	}

	bindCorrectContext() {
		this.createRouterEndpoints = this.createRouterEndpoints.bind(this);
		this.doInit = this.doInit.bind(this);
		this.getPreferences = this.getPreferences.bind(this);
		this.retrievePreferences = this.retrievePreferences.bind(this);
		this.setPreference = this.setPreference.bind(this);
		this.setPreferencesOnConfigStore = this.setPreferencesOnConfigStore.bind(this);
	}

	doInit(callback) {
		var self = this;
		FSBLDependencyManager.onAuthorizationCompleted(() => {
			asyncSeries([
				function routerEndpoints(done) {
					self.createRouterEndpoints();
					done();
				},
				function getPreferences(done) {
					self.retrievePreferences(done);
				},
				function setPreferences(done) {
					self.setPreferencesOnConfigStore(done);
				}
			], callback);
		});
	}

	/**
	 * Returns the userPreferences object that's saved on the service.
	 * @param {function} cb
	 */
	getPreferences(cb) {
		cb(null, this.userPreferences);
	};

	/**
	 * Retrieves preferences from storage and caches them on the service.
	 * @param {*} done
	 */
	retrievePreferences(callback) {
		StorageClient.get({ topic: STORAGE_TOPIC, key: USER_PREFERENCES_STORAGE_KEY }, (err, data) => {
			if (err) {
				Logger.system.error(err);
			}
			if (data) {
				this.userPreferences = data;
				Logger.system.log("APPLICATION LIFECYCLE:User Preferences Retrieved", data);
			} else {
				Logger.system.log("APPLICATION LIFECYCLE:No user preferences found");
			}
			callback();
		});
	};

	/**
	 * API Endpoint; given a path and a value, calls setValue on the configClient and saves the data to storage.
	 * @param {object} params
	 * @param {string} params.field which preference to set (e.g., "finsemble.services.docking...")
	 * @param {any} params.value value of the preference.
	 * @param {*} cb
	 */
	setPreference(params, cb) {
		var self = this;
		//Set the value in the config store and persist the value to storage. When both are done, invoke the callback telling the user that all is well - or not. Error gets passed back too.
		asyncParallel([
			function setValueInStore(done) {
				ConfigClient.setValue(params, done);
			},
			function persistValue(done) {
				self.userPreferences[params.field] = params.value;
				StorageClient.save({
					topic: STORAGE_TOPIC,
					key: USER_PREFERENCES_STORAGE_KEY,
					value: self.userPreferences
				}, (err) => {
					if (err) {
						Logger.system.error(err);
					}
					done();
				});
			}
		], cb);
	};

	/**
	 * Takes the preferences that were retrieved from storage, and sets them in the configStore.
	 * @param {callback} done
	 */
	setPreferencesOnConfigStore(callback) {
		var self = this;
		function setPreferenceOnConfig(field, done) {
			let preference = {
				field: field,
				value: self.userPreferences[field]
			};
			ConfigClient.setValue(preference, done);
		}
		Logger.system.debug("PreferenceService: setPreferencesOnConfigStore");
		let preferences = Object.keys(self.userPreferences);
		if (preferences.length) {
			asyncEach(preferences, setPreferenceOnConfig, callback);
		} else {
			callback();
		}
	};
}

export default new PreferenceService({ name: "preferencesService" });



