// This file contains the Finsemble config service
//   1) this service processes / builds the overall configuration object
//   2) it also responds to queries for config from other services and components.

"use strict";
import { BaseService } from "../baseService";
import RouterClient from "../../clients/routerClientInstance";
import DistributedStoreClient from "../../clients/distributedStoreClient";
import { ConfigUtilInstance as ConfigUtil } from "../../common/configUtil";
import Logger from "../../clients/logger";
/** preferencesService is snuck in through this import.
 * @TODO - Make it into a proper service, or absorb its function into configService.
 */
import "./preferencesService";
import UserNotification from "../../common/userNotification";
import { series as asyncSeries, each as asyncEach } from "async";

//this will need to be start eventually?
DistributedStoreClient.initialize();
Logger.start();

Logger.system.log("Starting Config Service");

/**
 * ConfigService. This service process and builds the overall configuration object.
 * It also response to queries for config from other services and components.
 */
class ConfigService extends BaseService {
	/**
	 * @param {string} params.name name of the service
	 * @param {object} params.startupDependencies
	 * @param {array} params.startupDependencies.clients Clients to wait on before initializing the service
	 * @param {array} params.startupDependencies.services Services to wait on before initializing the service
	 * @param {object} params.shutdownDependencies
	 * @param {array} params.shutdownDependencies.services Services to wait on before shutting down the service
	 **/
	constructor(params) {
		super(params);
		this.configStore = {};
		this.manifest = {}; // holds the complete processed manifest
		this.listeners = {};
		this.bindCorrectContext();
		this.onBaseServiceReady(this.doInit);
		this.start();
	}

	/**
	 * Creates the public interface with the rest of the application.
	 * Each of the handlers below simply receive a request, pass it off to a member function that does the work, and then sends the result back to the function that sent the original message (if necessary).
	 * @memberof ConfigService
	 */
	createRouterEndpoints() {
		//comment
		var self = this;
		// Old Config Handlers
		RouterClient.addResponder("config.get", this.get);
		RouterClient.addResponder("config.set", this.set);
		RouterClient.addResponder("config.processAndSet", this.processAndSet);
		RouterClient.addPubSubResponder("Config.changeNotification", { newConfig: null, finsemble: this.manifest.finsemble });

		// New Handlers for Local Store
		RouterClient.addPubSubResponder(/configService*/, {});

		RouterClient.addResponder("configService.getValue", function (err, message) {
			if (err) { return Logger.system.error(err); }
			return self.getValue(message.data, function (err, data) { message.sendQueryResponse(err, data); });
		});

		RouterClient.addResponder("configService.getValues", function (err, message) {
			if (err) { return Logger.system.error(err); }
			return self.getValues(message.data.fields, function (err, data) { message.sendQueryResponse(err, data); });
		});

		RouterClient.addResponder("configService.setValue", function (err, message) {
			self.setValue(message, function (err) {
				message.sendQueryResponse(err, message.data);
			});
		});

		RouterClient.addResponder("configService.setValues", function (err, message) {
			self.setValues(message, function (err) {
				message.sendQueryResponse(err, message.data);
			});
		});


		RouterClient.addResponder("configService.addListener", function (err, message) {
			self.addListener(message.data);
			message.sendQueryResponse(err, message.data);
		});
	}

	/**
	 * Will bind any event handlers. This can be an array for simplicity.
	 * @memberof MyService
	 */
	bindCorrectContext() {
		this.getImportFile = this.getImportFile.bind(this);
		this.processFinsembleConfig = this.processFinsembleConfig.bind(this);
		this.processManifest = this.processManifest.bind(this);
		this.initializeDataStore = this.initializeDataStore.bind(this);
		this.doInit = this.doInit.bind(this);
		this.get = this.get.bind(this);
		this.set = this.set.bind(this);
		this.processAndSet = this.processAndSet.bind(this);
		this.getValue = this.getValue.bind(this);
		this.getValues = this.getValues.bind(this);
		this._setValue = this._setValue.bind(this);
		this.setValue = this.setValue.bind(this);
		this.setValues = this.setValues.bind(this);
		this.addListener = this.addListener.bind(this);
		this.handleListeners = this.handleListeners.bind(this);
		this.confirmConfigAndReport = this.confirmConfigAndReport.bind(this);
	}

	/**
	 * Handles the startup-requirements of the service.
	 * @memberof MyService
	 */
	doInit(callback) {
		var self = this;
		asyncSeries([
			function getManifest(done) {
				ConfigUtil.getInitialManifest(function (initialManifest) {
					self.processManifest(initialManifest, function (completeManifest) {
						let doneManifest = completeManifest;
						RouterClient.transmit("ServiceManager.InitialManifestReceived", doneManifest);
						Logger.system.log("forceObjectsToLogger", "APPLICATION LIFECYCLE:STARTUP: ************* FULL PROCESSED MANIFEST *************", doneManifest);
						console.log("APPLICATION LIFECYCLE:STARTUP: ************* FULL PROCESSED MANIFEST *************", doneManifest);
						self.confirmConfigAndReport(doneManifest);
						self.manifest = doneManifest;
						done();
					});
				});
			},
			function waitForStoreClient(done) {
				DistributedStoreClient.onReady(done);
			},
			function createStore(done) {
				self.initializeDataStore(self.manifest, function () {
					Logger.system.debug("initializeDataStore complete");
					self.createRouterEndpoints();
					done();
				});
			}
		], callback);
	}

	// async read of JSON config file
	getImportFile(importURL, importCallback) {
		Logger.system.debug("fetching import " + importURL);
		fetch(importURL, {
			credentials: "include"
		}).then(function (response) {
			return response.json();
		}).catch(function (err) {
			importCallback("failure importing " + err, null);
		}).then(function (importObject) {
			importCallback(null, importObject);
		});
	}

	/**
	 *	Process a set of finsemble config properties, adding them to manifest.finsemble.  This config being processed
	 * may have other config URLs to asynchronously pull in; given any referenced config URLs contains more config properties
	 * this function is recursive (traveling depth-first through imported URLs).
	 * @param {object} params
	 * @param {object} params.manifest  manifest object containing finsemble property that will be updated with new config
	 * @param {boolean} params.allowOverwrites if true then new config is allowed to overwrite old, otherwise override cause error
	 * @param {number} params.recursionLevel only for diagnostic output plus recognizing if in a loop importing URLs
	 * @param {object} params.finsembleConfigObject the finsemble config to process (i.e. add to main.finsemble)
	 * @param {boolean} params.replaceComponents if true then replace preexisting component definitions
	 * @param {boolean} params.replaceServices if true then replace preexisting service definitions
	 * @param {boolean} params.updateStore if true then update the store as config is set (this happens when newConfig is defined through config.set())
	 * @param {function} callback
	 * @private
	*/
	processFinsembleConfig(params, callback) {
		let { manifest, allowOverwrites, recursionLevel, finsembleConfigObject, replaceComponents, replaceServices, updateStore } = params;
		let self = this;
		var thisFileRecursionLevel = recursionLevel; // save value at this level so counting depth only; otherwise recursiveLevel will count all imports
		ConfigUtil.resolveConfigVariables(manifest.finsemble, finsembleConfigObject); // resolve variable within finsemble config object
		Logger.system.debug("forceObjectsToLogger", "*** entering processFinsembleConfig ***", finsembleConfigObject, "recursionLevel", recursionLevel);
		var commentRegex = /comment.*/; // "comment" properties

		// removes all but system components from config (support config.set with replaceComponents)
		function filterNonSystemComponents(configObject) {
			var changed = false;
			for (var key in configObject) {
				// any component without explicit config where component.category === "system" is considered a non-system component and
				//deleted.  Therefore, if component or component.category properties are not defined, must be a non-system component
				if (!configObject[key].component || !configObject[key].component.category || configObject[key].component.category !== "system") {
					Logger.system.debug("filterNonSystemComponents deleting", key);
					delete configObject[key];
					changed = true;
				}
			}
			Logger.system.debug("filterNonSystemComponents after", configObject, changed);
			return changed;
		}
		// removes all but system services from config (support config.set with replaceServices)
		function filterNonSystemServices(configObject) {
			var changed = false;
			for (var key in configObject) {
				if (!configObject[key].category || configObject[key].category !== "system") {
					Logger.system.debug("filterNonSystemServices deleting", key);
					delete configObject.key;
					changed = true;
				}
			}
			Logger.system.debug("filterNonSystemServices after", configObject, changed);
			return changed;
		}

		// if updateStore is true then update store field with new value
		function conditionalStoreValue(updateStore, field, value) {
			if (updateStore) {
				self.configStore.setValue({ field, value });
			}
		}

		/**
		 *  Processes either importConfig object (which allows overwrites) or importThirdPartyConfig object (which doesn't allow overwrites)
		 *
		 * @param {object} configImportObject
		 * @param {boolean} allowOverwrites
		 * @param {function} processImportConfigCallback
		 * @private
		 */
		function processImportConfig(configImportObject, allowOverwrites, processImportConfigCallback) {
			thisFileRecursionLevel++;
			configImportObject = configImportObject ? configImportObject : [];
			Logger.system.debug("forceObjectsToLogger", "* processImportConfig *", configImportObject, "allowOverwrites", allowOverwrites);

			// processURL() is recursive to asynchronously step though each URL in import list before starting the next;
			// it is important to complete one at a time since there may be order dependencies (i.e. later config may replace earlier)
			function processURL(urlIndex, importCount) {
				Logger.system.debug("forceObjectsToLogger", "processURL", urlIndex, "importCount", importCount);
				if (urlIndex >= importCount) { // if no more import URLS to process in file, then invoke callback
					Logger.system.debug("forceObjectsToLogger", "---> importing all URLs complete", recursionLevel);
					if (processImportConfigCallback) { // if callback defined then invoke it
						processImportConfigCallback(manifest);
					}
				} else { // process a URL
					var currentImportURL = configImportObject[urlIndex];
					self.getImportFile(currentImportURL, function (error, newFinsembleConfigObject) {
						if (!error) {
							Logger.system.debug("forceObjectsToLogger", "---> importing into finsemble config", currentImportURL, "recursionLevel", recursionLevel);

							// recursive invocation since formats of all imports (which may be nested at each level) are the same as parent
							let PFCParams = {
								manifest: manifest,
								allowOverwrites: allowOverwrites,
								recursionLevel: thisFileRecursionLevel,
								finsembleConfigObject: newFinsembleConfigObject,
								replaceComponents: replaceComponents,
								replaceServices: replaceServices,
								updateStore: updateStore
							};
							self.processFinsembleConfig(PFCParams, function (manifest) {
								Logger.system.debug("forceObjectsToLogger", "updated manifest with configObject", currentImportURL, manifest);
								// done with last URL at this level of processFinsembleConfig recursion, so start processing the next URL at this level
								processURL(++urlIndex, importCount);
							});
						} else {
							Logger.error("forceObjectsToLogger", "failed importing into finsemble config", currentImportURL, error);
							processURL(++urlIndex, importCount);
						}
					});
				}
			}

			processURL(0, configImportObject.length); // start processing JSON URLs at index 0
		}

		for (var key in finsembleConfigObject) {

			if (commentRegex.test(key)) { // delete any comment properties
				Logger.system.debug("ignoring comment", key);
				delete finsembleConfigObject.key;

			} else if (key === "components") {
				if (allowOverwrites && replaceComponents) {
					let changed = filterNonSystemComponents(manifest.finsemble.components);
					if (changed && updateStore) {
						self.configStore.setValue({ field: "finsemble.components", value: manifest.finsemble.components });
					}
					replaceComponents = false; // first time replace then start adding to
				} else {
					manifest.finsemble.components = manifest.finsemble.components || {};
				}
				for (var newComponent in finsembleConfigObject.components) {
					Logger.system.debug("adding component", newComponent);
					if (!allowOverwrites && manifest.finsemble.components.hasOwnProperty(newComponent)) {
						console.warn("finsemble component config", newComponent, "cannot be overwritten by third-party component config", finsembleConfigObject.components[newComponent]);
					} else if (recursionLevel > 0 && manifest.finsemble.components.hasOwnProperty(newComponent)) {
						Logger.system.info("finsemble component config", newComponent, "is being overwritten by component config", finsembleConfigObject.components[newComponent]);
						manifest.finsemble.components[newComponent] = finsembleConfigObject.components[newComponent];
						conditionalStoreValue(updateStore, "finsemble.components." + newComponent, finsembleConfigObject.components[newComponent]);
					} else {
						manifest.finsemble.components[newComponent] = finsembleConfigObject.components[newComponent];
						conditionalStoreValue(updateStore, "finsemble.components." + newComponent, finsembleConfigObject.components[newComponent]);
					}
				}

			} else if (key === "services") {
				if (allowOverwrites && replaceServices) {
					let changed = filterNonSystemServices(manifest.finsemble.services);
					if (changed && updateStore) {
						self.configStore.setValue({ field: "finsemble.services", value: manifest.finsemble.services });
					}
					replaceServices = false; // first time replace then start adding to
				} else {
					manifest.finsemble.services = manifest.finsemble.services || {};
				}
				for (var newService in finsembleConfigObject.services) {
					Logger.system.debug("adding service", newService);
					if (!allowOverwrites && manifest.finsemble.services.hasOwnProperty(newService)) {
						console.warn("finsemble service config", newService, "cannot be overwritten by third-party service config", finsembleConfigObject.services[newService]);
					} else if (recursionLevel > 0 && manifest.finsemble.services.hasOwnProperty(newService)) {
						Logger.system.info("finsemble service config", newService, "is being overwritten by service config", finsembleConfigObject.services[newService]);
						manifest.finsemble.services[newService] = finsembleConfigObject.services[newService];
						conditionalStoreValue(updateStore, "finsemble.services." + newService, finsembleConfigObject.services[newService]);
					} else {
						manifest.finsemble.services[newService] = finsembleConfigObject.services[newService];
						conditionalStoreValue(updateStore, "finsemble.services." + newService, finsembleConfigObject.services[newService]);
					}
				}

			} else if (key === "workspaceTemplates") {
				manifest.finsemble.workspaceTemplates = manifest.finsemble.workspaceTemplates || {};
				for (var newTemplate in finsembleConfigObject.workspaceTemplates) {
					Logger.system.debug("adding template", newTemplate);
					if (!allowOverwrites && manifest.finsemble.workspaceTemplates.hasOwnProperty(newTemplate)) {
						console.warn("finsemble template config", newTemplate, "cannot be overwritten by third-party template config", finsembleConfigObject.workspaceTemplates[newTemplate]);
					} else if (recursionLevel > 0 && manifest.finsemble.workspaceTemplates.hasOwnProperty(newTemplate)) {
						Logger.system.info("finsemble template config", newTemplate, "is being overwritten by template config", finsembleConfigObject.workspaceTemplates[newTemplate]);
						manifest.finsemble.workspaceTemplates[newTemplate] = finsembleConfigObject.workspaceTemplates[newTemplate];
						conditionalStoreValue(updateStore, "finsemble.workspaceTemplates." + newTemplate, finsembleConfigObject.workspaceTemplates[newTemplate]);
					} else {
						manifest.finsemble.workspaceTemplates[newTemplate] = finsembleConfigObject.workspaceTemplates[newTemplate];
						conditionalStoreValue(updateStore, "finsemble.workspaceTemplates." + newTemplate, finsembleConfigObject.workspaceTemplates[newTemplate]);
					}
				}

			} else if (key === "importConfig") {
				// doing nothing on first pass -- process importConfig URLs separately at the end (see below)

			} else if (key === "importThirdPartyConfig") {
				// doing nothing on first pass -- process importThirdPartyConfig URLs separately at the end (see below)

			} else {
				Logger.system.debug("adding key", key);
				if (!allowOverwrites && manifest.finsemble.hasOwnProperty(key)) {
					console.warn("finsemble config property", key, "cannot be overwritten by third-party config");
				} else if (recursionLevel > 0 && manifest.finsemble.hasOwnProperty(key)) {
					Logger.system.info("finsemble config property", key, "is being overwritten");
					manifest.finsemble[key] = finsembleConfigObject[key];
					conditionalStoreValue(updateStore, "finsemble." + key, finsembleConfigObject[key]);
				} else {
					manifest.finsemble[key] = finsembleConfigObject[key];
					conditionalStoreValue(updateStore, "finsemble." + key, finsembleConfigObject[key]);
				}
			}
		}

		// if recursion has descended this deep then probably circular imports, so stop importing
		if (recursionLevel > 50) { // note this is the recursive depth of imports, not the import count (so should never happen unless import loop)
			let errorMsg = "Configuration Error: importConfig may have a loop (recursion depth exceeded).";
			Logger.error(errorMsg);
			let notificationURL = ConfigUtil.getDefault(manifest, "manifest.finsemble.notificationURL", manifest.finsemble.moduleRoot + "/components/system/notification/notification.html");
			UserNotification.alert("dev", "ONCE-SINCE-STARTUP", "FSBL-Internal-Config-Error", errorMsg, { url: notificationURL });

			// otherwise, process the importConfig URLs if there are any (this part is more complex because of recursive nature of the problem)
		} else {
			//import dynamic config, 3rd party config, and then user preferences. at the end, return the manifest.
			asyncSeries([
				function importDynamicConfig(done) {
					processImportConfig(finsembleConfigObject.importConfig, true, done);
				},
				function importThirdPartyConfig(done) {
					processImportConfig(finsembleConfigObject.importThirdPartyConfig, false, done);
				}
			],
				function configProcessingComplete() {
					callback(manifest);
				});
		}
	}

	/**
	 * the main routine kicking config processing
	 *
	 * @param {object} manifest
	 * @param {function} processManifestCallback
	 * @private
	 */
	processManifest(manifest, processManifestCallback) {
		if (typeof (manifest.finsemble) !== "undefined") {
			// assemble/update the finsemble config object (first time this is call the finsemble object updates itself, but simpler code)
			let PFCParams = {
				manifest: manifest,
				allowOverwrites: true,
				recursionLevel: 0,
				finsembleConfigObject: manifest.finsemble,
				replaceComponents: false,
				replaceServices: false,
				updateStore: false
			};
			this.processFinsembleConfig(PFCParams, function (manifest) {
				processManifestCallback(manifest);
			});
		} else {
			Logger.error("finsemble property not found in manifest");
		}
	}

	/**
	 *
	 *
	 * @param {object} manifest
	 * @param {function} callback
	 */
	initializeDataStore(manifest, callback) {
		var self = this;
		Logger.system.debug("initializeDataStore");
		DistributedStoreClient.createStore({
			store: "Finsemble-Configuration-Store",
			global: false,
			values: manifest
		}, function (err, newStore) {
			if (err) {
				Logger.error("CreateStore Error", err);
			} else {
				self.configStore = newStore;
				Logger.system.log("initializeDataStore complete", self.configStore);
				callback();
			}
		});
	}

	get(err, queryEvent) {
		var field;
		var errMsg = null;

		function getValuesCallback(err, storeData) {
			if (err) {
				errMsg = "field " + field + " error: " + err;
				Logger.system.warn(errMsg);
				queryEvent.sendQueryResponse(errMsg, null);
			} else {
				Logger.system.log("get responseData", storeData);
				queryEvent.sendQueryResponse(errMsg, storeData);
			}
		}

		// data to be retrieved stored in configReference
		if (queryEvent.data && queryEvent.data.hasOwnProperty("field")) {
			field = queryEvent.data.field;
			this.configStore.getValues({ field }, getValuesCallback); // this case returns a sub-field in the store
		} else {
			this.configStore.getValues(getValuesCallback); // this case returns everything in the store
		}
	}

	set(err, queryEvent) {
		var self = this;
		function getValuesCallback(err, storeData) {
			if (err) {
				var errMsg = "field " + storeData + " error: " + err;
				Logger.system.warn(errMsg);
				queryEvent.sendQueryResponse(errMsg, null);
			} else {
				Logger.system.log("get responseData", storeData);
				queryEvent.sendQueryResponse(errMsg, storeData);
			}
		}

		if (queryEvent.data && queryEvent.data.hasOwnProperty("field")) {
			if (!queryEvent.data.field.startsWith("finsemble.")) {
				queryEvent.sendQueryResponse("You cannot modify this part of the config", null);
			} else {
				self.configStore.setValues({ field: queryEvent.data.field, value: queryEvent.data.value });
				self.configStore.getValues({ field: "field" }, getValuesCallback);
			}
		} else {
			queryEvent.sendQueryResponse("Invalid Request", null);
		}
	}

	processAndSet(err, queryEvent) {
		var self = this;
		var trustedRequest = RouterClient.trustedMessage(queryEvent);
		var newConfig = queryEvent.data.newConfig;
		var overwrite = queryEvent.data.overwrite ? true : false;
		var replace = queryEvent.data.replace ? true : false;
		Logger.system.debug("forceObjectsToLogger", "Config.processAndSet", trustedRequest, newConfig);

		if (overwrite && !trustedRequest) {
			queryEvent.sendQueryResponse("Config.processAndSet Rejected: only same domain components using default SharedWorker transport can overwrite config", null);
		} else {
			let PFCParams = {
				manifest: self.manifest,
				allowOverwrites: overwrite,
				recursionLevel: 0,
				finsembleConfigObject: newConfig,
				replaceComponents: replace,
				replaceServices: replace,
				updateStore: true
			};
			self.processFinsembleConfig(PFCParams, function (manifest) {
				Logger.system.log("forceObjectsToLogger", "Config.processAndSet revised manifest", manifest); // this log referenced in FAQ so don't change
				// only by setting one last value and waiting for callback do we know all the previous setValues are done too
				self.configStore.setValue({ field: "finsemble.configSetDone", value: true }, function () {
					queryEvent.sendQueryResponse(null, manifest.finsemble);
				});
				RouterClient.publish("Config.changeNotification", { newConfig, finsemble: manifest.finsemble });
			});
		}
	}

	getValue(data, cb) {
		this.configStore.getValue(data, cb);
	}

	getValues(data, cb) {
		if (!data) {
			return this.configStore.getValues(cb);
		}
		this.configStore.getValues(data, cb);
	}

	_setValue(data, cb) {
		this.configStore.setValue(data, cb);
	}

	setValue(message, cb) {
		var trustedRequest = RouterClient.trustedMessage(message);
		if (trustedRequest) {
			this._setValue(message.data, cb);
		} else {
			cb("setValue failed. Only trusted (root domain) sources can set config.");
		}
	}

	setValues(message, cb) {
		var trustedRequest = RouterClient.trustedMessage(message);
		if (trustedRequest) {
			asyncEach(message.data, function (field, done) {
				_setValue(field, done);
			}, function (err) {
				cb(err);
			});
		} else {
			cb("setValues failed. Only trusted (root domain) sources can set config.");
		}
	}

	addListener(data) {
		if (!this.listeners[data]) {
			this.listeners[data] = true;
			this.configStore.addListener(data == "configService" ? null : { field: data.replace("configService.", "") }, this.handleListeners);
		}
		//cb(null, data);
	}

	handleListeners(err, data) {
		data.field = data.field.replace("Finsemble-Configuration-Store", "configService");
		RouterClient.publish(data.field, data);
	}

	confirmConfigAndReport(manifest) {
		// definitions to drive verification of config object

		const REQUIRED_STRING = ConfigUtil.REQUIRED_STRING;
		const REQUIRED_OBJECT = ConfigUtil.REQUIRED_OBJECT;
		const OPTIONAL_EXPECTED_ARRAY = ConfigUtil.OPTIONAL_EXPECTED_ARRAY;
		const OPTIONAL_EXPECTED_STRING = ConfigUtil.OPTIONAL_EXPECTED_STRING;
		const OPTIONAL_EXPECTED_OBJECT = ConfigUtil.OPTIONAL_EXPECTED_OBJECT;
		const DEPRECATED_BOOLEAN = ConfigUtil.DEPRECATED_BOOLEAN;
		const DEPRECATED_STRING = ConfigUtil.DEPRECATED_STRING;

		// This object drives verification using verifyConfigObject().
		// Rechecking some of config verified in ServiceManager because extra processing beyond ServiceManager's bootstrap config (e.g.importConfig, etc) /
		var configVerifyObject = {
			finsemble: {
				applicationRoot: REQUIRED_STRING,
				moduleRoot: REQUIRED_STRING,
				system: {
					FSBLVersion: REQUIRED_STRING,
					requiredServicesConfig: REQUIRED_OBJECT,
				},
				router: {
					sameDomainTransport: OPTIONAL_EXPECTED_STRING,
					crossDomainTransport: OPTIONAL_EXPECTED_STRING,
				},
				services: REQUIRED_OBJECT,
				workspaceTemplates: REQUIRED_OBJECT,
				isAuthEnabled: DEPRECATED_BOOLEAN,
				splinteringConfig: {
					splinterAgents: OPTIONAL_EXPECTED_ARRAY
				},
				servicesConfig: OPTIONAL_EXPECTED_OBJECT,
				betaFeatures: REQUIRED_OBJECT,
				components: REQUIRED_OBJECT,
				forceRouterToOFB: DEPRECATED_BOOLEAN
			}
		};

		var configOk = ConfigUtil.verifyConfigObject("manifest", manifest, configVerifyObject);

		if (!configOk) {
			let errorMsg = "Configuration Error: See log errors for more information.";
			Logger.system.error("forceObjectsToLogger", errorMsg, "manifest.finsemble", manifest.finsemble);
			let notificationURL = ConfigUtil.getDefault(manifest, "manifest.finsemble.notificationURL", manifest.finsemble.moduleRoot + "/components/system/notification/notification.html");
			UserNotification.alert("dev", "ONCE-SINCE-STARTUP", "FSBL-Internal-Config-Error", errorMsg, { url: notificationURL });
		}
	}
}

export default new ConfigService({ name: "configService" });



